import { Injectable } from '@angular/core';
import { DocumentRefService } from './document-ref.service';
import { WindowRefService } from './window-ref.service';

@Injectable()
export class DocumentDownloadService {
  constructor(
    private documentRefService: DocumentRefService,
    private windowRefService: WindowRefService
  ) {}

  downloadDocument(blob, documentName): void {
    const download = this.documentRefService.nativeDocument.createElement('a');
    this.documentRefService.nativeDocument.body.appendChild(download);
    if (
      this.windowRefService.nativeWindow.navigator.appVersion
        .toString()
        .includes('Trident')
    ) {
      this.windowRefService.nativeWindow.navigator.msSaveBlob(
        blob,
        documentName
      );
    } else {
      // for other browsers
      const fileURL = this.windowRefService.nativeWindow.URL.createObjectURL(
        blob
      );
      download.href = fileURL;
      download.download = documentName;
      download.click();
      download.remove();
    }
  }
}
