import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'sofReplaceAll' })
export class ReplaceAllPipe implements PipeTransform {
  constructor() {}

  transform<T>(text: string, toReplace: string, replaceWith: string): string {
    if (text) {
      return text.split(toReplace).join(replaceWith);
    } else {
      return text;
    }
  }
}
