import { CommonModule } from '@angular/common';
import {
  ModuleWithProviders,
  NgModule,
  Optional,
  SkipSelf
} from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { PageTitleConfig } from './page-title.config';
import { PageTitleService } from './page-title.service';
import { PAGE_TITLE_TOKEN } from './page-title.token';

@NgModule({
  imports: [CommonModule, TranslateModule],
  providers: [PageTitleService]
})
export class PageTitleModule {
  constructor(@Optional() @SkipSelf() parentModule: PageTitleModule) {
    if (parentModule) {
      throw new Error(
        'PageTitleModule is already loaded. Import in your base AppModule only.'
      );
    }
  }

  /**
   * Optional configuration
   * @param pageTitleConfig some basic settings used by PageTitleService
   */
  static forRoot(
    pageTitleConfig: PageTitleConfig
  ): ModuleWithProviders<PageTitleModule> {
    return {
      ngModule: PageTitleModule,
      providers: [
        {
          provide: PAGE_TITLE_TOKEN,
          useValue: pageTitleConfig
        }
      ]
    };
  }
}
