import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { validatorAddress, validatorPhone } from '../utils/validators.util';

@Injectable()
export class FormGroupService {
  constructor(private fb: FormBuilder) {}

  createPhoneGroup(defaultCountry: { countryId: string } = null): FormGroup {
    return this.fb.group(
      {
        country: [defaultCountry],
        zonalPhoneNumber: [''],
        localPhoneNumber: ['']
      },
      {
        validator: [validatorPhone()]
      }
    );
  }

  createAddressGroup(defaultCountry: { countryId: string } = null): FormGroup {
    return this.fb.group(
      {
        country: [defaultCountry],
        postalCodeAndCity: [null],
        addressLineOne: [''],
        addressLineTwo: ['']
      },
      {
        validator: [validatorAddress()]
      }
    );
  }
}
