import { AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { isNullOrUndefined } from '../utils/is-null-or-undefined.util';
import { isNumber } from '../utils/is-number.util';

export function maxLengthNumberValidator(max: number): ValidatorFn {
  return (control: AbstractControl): ValidationErrors => {
    const number: number = control.value;

    if (isNullOrUndefined(number) || !isNumber(number)) {
      return null;
    }

    return number.toString().length > max ? { maxLengthNumber: max } : null;
  };
}
