import { AbstractControl, ValidatorFn } from '@angular/forms';

export function validatorPhone(
  validationMessage: string = 'ERROR_PHONE'
): ValidatorFn {
  return (c: AbstractControl) => {
    const { country, zonalPhoneNumber, localPhoneNumber } = c.value;

    if (
      (zonalPhoneNumber && (!country || !localPhoneNumber)) ||
      (localPhoneNumber && (!country || !zonalPhoneNumber))
    ) {
      return {
        phone: {
          validationMessage
        }
      };
    }

    return null;
  };
}

export function validatorAddress(
  validationMessage: string = 'ERROR_ADDRESS'
): ValidatorFn {
  return (c: AbstractControl) => {
    const {
      country,
      postalCodeAndCity,
      addressLineOne,
      addressLineTwo
    } = c.value;

    if (
      (postalCodeAndCity &&
        postalCodeAndCity.postalCode &&
        (!country || !postalCodeAndCity.city || !addressLineOne)) ||
      (postalCodeAndCity &&
        postalCodeAndCity.city &&
        (!country || !postalCodeAndCity.postalCode || !addressLineOne)) ||
      (addressLineOne &&
        (!country ||
          !postalCodeAndCity ||
          !postalCodeAndCity.postalCode ||
          !postalCodeAndCity.city))
    ) {
      return {
        address: {
          validationMessage
        }
      };
    }

    return null;
  };
}
