import { Pipe, PipeTransform } from '@angular/core';
import { deepFetchProperty } from '../../utils/deep-fetch-property.util';

@Pipe({ name: 'sofDeepFetchProperty' })
export class DeepFetchPropertyPipe implements PipeTransform {
  transform(item: any, propertyPath): any {
    return deepFetchProperty(item, propertyPath);
  }
}
