import { NgModule, Optional, SkipSelf } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { DocumentDownloadService } from './document-download.service';
import { DocumentRefService } from './document-ref.service';
import { FileSelectionService } from './file-selection.service';
import { FormGroupService } from './form-group.service';
import { ObjectService } from './object.service';
import { RouterToolsService } from './router-tools.service';
import { ScrollService } from './scroll.service';
import { WindowRefService } from './window-ref.service';

@NgModule({
  providers: [
    WindowRefService,
    DocumentRefService,
    DocumentDownloadService,
    FileSelectionService,
    FormGroupService,
    ObjectService,
    RouterToolsService,
    ScrollService
  ]
})
export class UtilServicesModule {
  constructor(
    @Optional() @SkipSelf() parentModule: UtilServicesModule,
    @Optional() router: Router,
    @Optional() reactiveForms: FormBuilder,
    @Optional() translate: TranslateService
  ) {
    if (parentModule) {
      throw new Error(
        'UtilServicesModule is already loaded. Import in your base AppModule only.'
      );
    }
    if (!router) {
      throw new Error('You need to import the RouterModule in your AppModule!');
    }
    if (!reactiveForms) {
      throw new Error(
        'You need to import the ReactiveFormsModule in your AppModule!'
      );
    }
    if (!translate) {
      throw new Error(
        'You need to import the TranslateModule in your AppModule!'
      );
    }
  }
}
