/**
 * Sorts an object and all its sub-objects based on the key-value
 */
export function sortObject(o: any): any {
  return Object.keys(o)
    .sort()
    .reduce((acc, curr, i) => {
      return {
        ...acc,
        [curr]: typeof o[curr] === 'object' && o[curr] !== null ? sortObject(o[curr]) : o[curr]
      };
    }, {});
}
