export function getConfigurationParameters(
  environment: any,
  apiPath: string
): { basePath: string } {
  const baseUrl = (window as any).config
    ? ((window as any).config.api?.baseUrl ?? `https://${(window as any).config.base.domain}/api`)
    : ((environment.config as any).api?.baseUrl ?? `https://${(environment.config as any).base?.domain}/api`);
  const addTrailingFlash = baseUrl.substr(-1) === '/' ? '' : '/';
  return {
    basePath: `${baseUrl}${addTrailingFlash}${apiPath}`
  };
}
