import {
  ChangeDetectorRef,
  OnDestroy,
  Pipe,
  PipeTransform
} from '@angular/core';
import { slice } from 'lodash';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { fromEvent, merge, Observable, Subject, Subscription } from 'rxjs';

import { async } from 'rxjs/internal/scheduler/async';
import {
  auditTime,
  filter,
  map,
  mapTo,
  scan,
  startWith,
  switchMap
} from 'rxjs/operators';
import { DocumentRefService } from '../../services/document-ref.service';
import { WindowRefService } from '../../services/window-ref.service';

@UntilDestroy()
@Pipe({ name: 'sofInfiniteScroll', pure: false })
export class InfiniteScrollPipe implements PipeTransform, OnDestroy {
  private auditTime = 500;
  private preloadHeight = 500;
  private scheduler = async;
  private elementRef: HTMLElement;
  private thresholdNumberOfItems = 35;
  private limitedDataSet: any[];
  private dataSet: any[];
  private subscription: Subscription;

  private scrollOrResize$: Observable<Event>;
  private pageByScrollOrResize$: Observable<boolean>;
  private numberOfItems$: Observable<number>;
  private dataSet$ = new Subject<any[]>();

  constructor(
    private windowRefService: WindowRefService,
    private documentRefService: DocumentRefService,
    private changeDetectorRef: ChangeDetectorRef
  ) {}

  transform(
    value: any[],
    thresholdNumberOfItems?: number,
    preloadHeight?: number,
    auditTimeValue?: number,
    elementRef?: HTMLElement
  ): any {
    if (value === null || value === undefined) {
      return value;
    }

    if (!Array.isArray(value)) {
      throw new Error(
        'sofInfiniteScroll only accepts: null, undefined or any[]'
      );
    }

    if (
      thresholdNumberOfItems &&
      this.thresholdNumberOfItems !== thresholdNumberOfItems
    ) {
      this.thresholdNumberOfItems = thresholdNumberOfItems;
    }
    if (elementRef && this.elementRef !== elementRef) {
      this.elementRef = elementRef;
    }
    // makes it hard to test when defined in the constructor
    if (!this.subscription) {
      this.scrollOrResize$ = this.getScrollOrResize$();
      this.pageByScrollOrResize$ = this.getPageByScrollOrResize$();
      this.numberOfItems$ = this.getNumberOfItems$();

      this.subscription = this.getLimitedDataSet$()
        .pipe(takeUntilDestroy(this))
        .subscribe(v => {
          this.limitedDataSet = v;
          this.changeDetectorRef.markForCheck();
        });
    }

    // only when the data set changes in an immutable way reset the data set
    // this is important because otherwise numberOfItems will be reset change after change
    // which results in the same limited data set when scrolling
    if (this.dataSet !== value) {
      this.dataSet = value;
      this.dataSet$.next(value);
    }

    if (preloadHeight && this.preloadHeight !== preloadHeight) {
      this.preloadHeight = preloadHeight;
    }

    if (auditTimeValue && this.auditTime !== auditTimeValue) {
      this.auditTime = auditTimeValue;
    }

    if (elementRef && this.elementRef !== elementRef) {
      this.elementRef = elementRef;
    }

    return this.limitedDataSet;
  }

  ngOnDestroy(): void {}

  private getScrollOrResize$(): Observable<Event> {
    return merge(
      fromEvent<Event>(
        this.elementRef || this.windowRefService.nativeWindow,
        'scroll'
      ),
      fromEvent<Event>(this.windowRefService.nativeWindow, 'resize')
    ).pipe(auditTime(this.auditTime, this.scheduler));
  }

  private getPageByScrollOrResize$(): Observable<boolean> {
    return this.scrollOrResize$.pipe(
      map(() => {
        const scrollTop =
          (this.elementRef && this.elementRef.scrollTop) ||
          (this.documentRefService.nativeDocument.documentElement &&
            this.documentRefService.nativeDocument.documentElement.scrollTop) ||
          this.documentRefService.nativeDocument.body.scrollTop;
        const scrollHeight =
          (this.elementRef && this.elementRef.scrollHeight) ||
          (this.documentRefService.nativeDocument.documentElement &&
            this.documentRefService.nativeDocument.documentElement
              .scrollHeight) ||
          this.documentRefService.nativeDocument.body.scrollHeight;

        return (
          scrollTop + this.windowRefService.nativeWindow.innerHeight >=
          scrollHeight - this.preloadHeight
        );
      }),
      filter(b => !!b)
    );
  }

  private getNumberOfItems$(): Observable<number> {
    return this.pageByScrollOrResize$.pipe(
      startWith(null),
      mapTo(this.thresholdNumberOfItems), // make sure scan doesn't get a boolean
      scan(acc => acc + this.thresholdNumberOfItems, 0)
    );
  }

  private getLimitedDataSet$(): Observable<any[]> {
    return this.dataSet$.pipe(
      switchMap(dataSet =>
        this.numberOfItems$.pipe(map(count => slice(dataSet, 0, count)))
      )
    );
  }
}
