import {
  HttpHandler,
  HttpInterceptor,
  HttpRequest
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { WindowRefService } from '../services/window-ref.service';
import { CacheOverrideConfigAbstract } from './cache-override-config.abstract';

@Injectable()
export class CacheOverrideInterceptor implements HttpInterceptor {
  // If we are in IE, or we do requests for JSON files (i18n) we do not want cache to be used.

  private isIE: boolean =
    this.windowRefService.nativeWindow.navigator.userAgent
      .toUpperCase()
      .indexOf('TRIDENT') >= 0;
  private blacklistedUrls = this.configService.getBlacklistedUrls();

  constructor(
    private windowRefService: WindowRefService,
    private configService: CacheOverrideConfigAbstract
  ) {}

  intercept(req: HttpRequest<any>, next: HttpHandler): any {
    if (!this.isIE) {
      return next.handle(req);
    }

    const blacklisted = !!this.blacklistedUrls.filter(url =>
      req.url.startsWith(url)
    ).length;

    if (blacklisted) {
      return next.handle(req);
    }

    const headers = req.headers
      .append('Cache-Control', 'no-cache')
      .append('Pragma', 'no-cache')
      .append('Expires', 'Sat, 01 Jan 2000 00:00:00 GMT');

    return next.handle(req.clone({ headers }));
  }
}
