import {
  ModuleWithProviders,
  NgModule,
  Optional,
  SkipSelf,
  Type
} from '@angular/core';
import { WindowRefService } from '../services/window-ref.service';
import { CacheOverrideConfigAbstract } from './cache-override-config.abstract';
import { CacheOverrideInterceptor } from './cache-override.interceptor';

@NgModule()
export class CacheOverrideModule {
  constructor(
    @Optional() @SkipSelf() parentModule: CacheOverrideModule,
    @Optional() windowRefService: WindowRefService,
    @Optional() cacheOverrideConfig: CacheOverrideConfigAbstract
  ) {
    if (parentModule) {
      throw new Error(
        'CacheOverrideModule is already loaded. Import in your base AppModule only with .forRoot(cacheOverrideConfig: Type<CacheOverrideConfigAbstract>).'
      );
    }
    if (!cacheOverrideConfig) {
      throw new Error(
        'CacheOverrideModule could not be loaded. You need to use the .forRoot(cacheOverrideConfig: Type<CacheOverrideConfigAbstract>) method and provide an implementation of CacheOverrideConfigAbstract'
      );
    }
    if (!windowRefService) {
      throw new Error(
        'You need to import the UtilServicesModule in your AppModule!'
      );
    }
  }

  static forRoot(
    cacheOverrideConfig: Type<CacheOverrideConfigAbstract>
  ): ModuleWithProviders<CacheOverrideModule> {
    return {
      ngModule: CacheOverrideModule,
      providers: [
        CacheOverrideInterceptor,
        {
          provide: CacheOverrideConfigAbstract,
          useClass: cacheOverrideConfig
        }
      ]
    };
  }
}
