import { NgModule, Optional, SkipSelf } from '@angular/core';
import { RoutingAllowedGuard } from './routing-allowed.guard';

@NgModule({
  providers: [RoutingAllowedGuard]
})
export class UtilGuardsModule {
  constructor(@Optional() @SkipSelf() parentModule: UtilGuardsModule) {
    if (parentModule) {
      throw new Error(
        'UtilGuardsModule is already loaded. Import in your base AppModule only.'
      );
    }
  }
}
