import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanDeactivate,
  RouterStateSnapshot
} from '@angular/router';
import { Observable } from 'rxjs';
import { RoutingAllowedInterface } from '../interfaces/routing-allowed.interface';

@Injectable()
export class RoutingAllowedGuard
  implements CanDeactivate<RoutingAllowedInterface> {
  canDeactivate(
    component: RoutingAllowedInterface,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState?: RouterStateSnapshot
  ): Observable<boolean> | boolean {
    return component.routingAllowed(currentState, nextState);
  }
}
