import { isNullOrUndefined } from './is-null-or-undefined.util';

export function dateWithoutTimeEquals(date1: Date, date2: Date): boolean {
  if (isNullOrUndefined(date1) && isNullOrUndefined(date2)) {
    return true;
  }
  if (!isNullOrUndefined(date1) && !isNullOrUndefined(date2)) {
    const cleanDate1 = getCleanDate(date1);
    const cleanDate2 = getCleanDate(date2);

    return cleanDate1.getTime() === cleanDate2.getTime();
  }
  return false;
}

export function dateWithoutTimeBefore(date1: Date, date2: Date): boolean {
  if (!isNullOrUndefined(date1) && !isNullOrUndefined(date2)) {
    const cleanDate1 = getCleanDate(date1);
    const cleanDate2 = getCleanDate(date2);

    return cleanDate1.getTime() < cleanDate2.getTime();
  }
  return false;
}

export function dateWithoutTimeAfter(date1: Date, date2: Date): boolean {
  if (!isNullOrUndefined(date1) && !isNullOrUndefined(date2)) {
    const cleanDate1 = getCleanDate(date1);
    const cleanDate2 = getCleanDate(date2);

    return cleanDate1.getTime() > cleanDate2.getTime();
  }
  return false;
}

export function timeWithoutDateEquals(time1: Date, time2: Date): boolean {
  if (isNullOrUndefined(time1) && isNullOrUndefined(time2)) {
    return true;
  }
  if (!isNullOrUndefined(time1) && !isNullOrUndefined(time2)) {
    const cleanTime1 = getCleanTime(time1);
    const cleanTime2 = getCleanTime(time2);

    return cleanTime1.getTime() === cleanTime2.getTime();
  }
  return false;
}

export function timeWithoutDateBefore(time1: Date, time2: Date): boolean {
  if (!isNullOrUndefined(time1) && !isNullOrUndefined(time2)) {
    const cleanTime1 = getCleanTime(time1);
    const cleanTime2 = getCleanTime(time2);

    return cleanTime1.getTime() < cleanTime2.getTime();
  }
}

export function timeWithoutDateAfter(time1: Date, time2: Date): boolean {
  if (!isNullOrUndefined(time1) && !isNullOrUndefined(time2)) {
    const cleanTime1 = getCleanTime(time1);
    const cleanTime2 = getCleanTime(time2);

    return cleanTime1.getTime() > cleanTime2.getTime();
  }
}

function getCleanTime(time: Date): Date {
  const cleanTime = new Date(time.getTime());

  cleanTime.setFullYear(2020, 0, 1);
  cleanTime.setMilliseconds(0);

  return cleanTime;
}

function getCleanDate(date: Date): Date {
  const cleanDate = new Date(date.getTime());

  cleanDate.setHours(0, 0, 0, 0);

  return cleanDate;
}
